.container {
    position: relative;
    width: 70.7%;
    height: 100%;
    border-radius: 8px;
    margin-bottom: 30px;
}

.box {
    align-items: center;
    background-color: #dedde1;
    width: 100%;
    height: 150px;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.foot {
    position: absolute;
    right: 0px;
    bottom: 0px;
    width: 10.5%;
}

h5 {
    text-align: center;
    position: absolute;
    right: 100px;
    bottom: 0px;
    font-style: italic;
}

@media only screen and (max-width: 770px) {
    .container {
        width: 100%;
        margin-bottom: 0px;
    }
    .foot {
        width: 25.5%;
    }
}