.container {
    position: relative;
    display: flex;
    justify-content: center;
    width: 70%;
    background-color: white;
    height: 100%;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    border: 0.3px solid grey;
    padding: 5px;
    margin-bottom: 5px;
}

@media only screen and (max-width: 770px) {
    .container {
        width: 100%;
    }
}