.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px 0;
    background-color: white;
    height: 50%;
    border-radius: 8px;
    width: 70%;
    border: 0.3px solid grey;
}

.box {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    background-color: #dedde1;
    width: 100%;
    height: 150px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.card {
    margin: 5% 2% !important;
    ;
}

.infected {
    border-bottom: 10px solid #4B7BF5;
}

.recovered {
    border-bottom: 10px solid #10BC10;
}

.deaths {
    border-bottom: 10px solid#DF362D;
}

.corona {
    width: 130px;
    margin-bottom: -14px;
}

.update {
    width: 150px;
    margin-bottom: 14px;
    margin-right: 5px;
}

@media only screen and (max-width: 770px) {
    .card {
        margin: 5% 0 !important;
    }
    .update {
        width: 100px;
    }
}